.vetskitchen-flavour-columns .wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 30px 0;
  text-align: center;
}
.vetskitchen-flavour-columns .wrapper-image {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100px;
  height: 100px;
}
.vetskitchen-flavour-columns .icon {
  width: auto;
  height: 100%;
}
.vetskitchen-flavour-columns .title {
  padding: 10px 0 20px;
  margin: 0;
  font-family: "Signika";
  font-weight: normal;
  font-style: normal;
  font-size: 30px;
  color: #5e5e5e;
}
.vetskitchen-flavour-columns .description {
  font-family: "Hind";
  font-weight: 300;
  font-size: 18px;
  color: #000;
}
@media (max-width: 575px) {
  .vetskitchen-flavour-columns .title {
    padding-bottom: 15px;
  }
}